@use "../../palette";

.flair {
    display: flex;
    flex-wrap: wrap;
    gap: 0.25em 1rem;

    &__item {
        display: flex;
        align-items: center;

        &.-hover {
            display: none;
        }
    }

    &.-inline {
        flex-wrap: nowrap;
    }

    .icon {
        display: block;
        text-decoration: none;

        + .text {
            margin-left: 0.5rem;
        }
    }

    &:empty {
        display: none;
    }
}

tr:hover .flair > .flair__item.-hover {
    display: inline-block;
}
